.day {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    position: relative;
    z-index: 1;
    cursor: pointer;
    -webkit-font-feature-settings: "tnum";
            font-feature-settings: "tnum";
    font-variant-numeric: tabular-nums;
    color: #212121;
}

@media only screen and (max-width:720px) {
    .day{
        width:35px;
        height:35px;
    }
}
.day.with-tasks::after{
    content:'';
    position: absolute;
    -webkit-align-self:flex-start;
            align-self:flex-start;
    top:25%;
    width:7%;
    height:7%;
    border-radius:100%;
    background:#E91E63;
}
.day.previous {
    color: grey;
}

.day.selected ,.day.previous.selected {
    color: white;
}

.day.selected.with-tasks::after{
    background:white;
}
.day.selected::before {
    content: '';
    position: absolute;
    background: #E91E63;
    width: 70%;
    height: 70%;
    box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.2);
    z-index: -1;
    border-radius: 50%;
    left: 14%;
    top: 15%;
    /* transform: scale(0);
    animation: select 0.5s ease-out forwards; */
}

@-webkit-keyframes select {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0)
    }
    100% {
        -webkit-transform: scale(1.0);
                transform: scale(1.0)
    }
}

@keyframes select {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0)
    }
    100% {
        -webkit-transform: scale(1.0);
                transform: scale(1.0)
    }
}
.week{
    display:grid;
    grid-template-columns: repeat(7,1fr);
    width:100%;
}
.week.upper-row{
    background:#C2185B;
    color:white;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
}
.week.upper-row > .day {
    color:white;
}
.calendar {
   
    align-self: center;
    justify-self: center;
   
    width: 100%;
    display: grid;
    grid-template-rows: 1fr 6fr;
    box-shadow: 0px 0px 5px rgba(180, 131, 165, 0.34);
    border-radius: 20px;
    transition: scale 1s;
}

@media only screen and (max-width:720px) {
    .calendar{
        margin-bottom:20px;
    }
}

.calendar-days-container{
    display:grid;
    position: relative;
    overflow: hidden;
}
.calendar-days {
    display:grid;
    grid-template-rows:6fr;
    width:100%;
    height:100%;
}

.calendar-days.previous, .calendar-days.next{
    position: absolute;
    left:0;
    top:0;
    width:100%;
    height:100%;
}
.calendar-days.previous{
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%)
}
.calendar-days.next{
    -webkit-transform: translateX(100%);
            transform: translateX(100%)
}
.todo-button {
    padding: 7px 7px;
    font-size: 0.8em;
    
    box-shadow:0px 0px 2px rgba(0,0,0,0.4);
    color: #212121;
    border-radius: 5px;
    cursor: pointer;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-size: 13px 13px;
    background-repeat: no-repeat;
    background-position: center center;
}

.todo-button.confirm {
    background-image: url(/react-hooks-todo/static/media/confirm.9cf15f6c.svg);
}

.todo-button.delete {
    background-image: url(/react-hooks-todo/static/media/delete.a15a5733.svg);
}
.todo-item {
    display: grid;
    grid-template-rows:auto;
    grid-template-columns: 60% 1fr 1fr;
    grid-column-gap: 10%;
    height:100%;
    -webkit-align-content: center;
            align-content: center;
    border-bottom: 1px solid rgba(0,0,0,0.3);
    padding:0 10px;
    grid-column: 2/3;

    
}
.todo-item-input{
    width:100%;
}
.todo-item-text{
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    -webkit-align-self: baseline;
            align-self: baseline;
    font-family:'Roboto';
    border:none;
    padding:5px;
    font-size:1em;

}
input{
    font-family:'Roboto'
}

.todo-header{
    display:-webkit-flex;
    display:flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    color:white;
    background:#C2185B;
}

.todo-new{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: center;
            justify-content: center;
    height:100%;
    -webkit-align-items: center;
            align-items: center;
    border-bottom: 1px solid rgba(0,0,0,0.3);
    padding:0 10px;
    grid-column: 2/3;
}

.todo-new-input{
    
    border:none;
    border-radius:5px;
    text-align: center;
    width:100%;
    height:100%;
    

}

.todo-new-input:focus{
   outline:none;

}
.todo-component{
    
    align-self: center;
    justify-self:left;
    display:grid;
    grid-template-rows: calc(14.28% - 2px) 6fr;
    box-sizing: border-box;
    box-shadow:0px 0px 5px rgba(0,0,0,0.2);
    border-radius:5px;
    border: 2px solid #C2185B;
    width:250px;
    height:420.5px;
    box-sizing: border-box;
    margin-left:20px;
    
    
}

@media only screen and (max-width:720px) {
    .todo-component{
        margin-left:0;
    }
}
.todo-body::-webkit-scrollbar{
    display:none;
}
.todo-body{
    display:grid;
    max-height:100%;
    grid-auto-rows: 50px;
    overflow-y:auto;
    grid-template-columns: 1fr 20fr 1fr;
}
.month-selection {
    grid-column: 2/3;
    display: grid;
    grid-template-columns: 5fr 1fr 100px 1fr 5fr;
}

.month-selection-months {
    grid-column: 3/4;
    position: relative;
    overflow: hidden;
}

.month-selection-month-name {
    text-align: center;
}

.month-selection-month-name.previous, .month-selection-month-name.next {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    text-align: center;
}
.month-selection-month-name.previous{
    -webkit-transform: translate(-100%);
            transform: translate(-100%)
}
.month-selection-month-name.next{
    -webkit-transform: translate(100%);
            transform: translate(100%)
}

.month-selection-button {
    padding: 10px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    cursor: pointer;
}

.month-selection-button.left {
    background-image: url(/react-hooks-todo/static/media/left.5bbac221.svg);
    grid-column: 2/3;
}

.month-selection-button.right {
    background-image: url(/react-hooks-todo/static/media/right.c042f731.svg);
    grid-column: 4/5;
}
